
import * as React from "react";
import { Button, Toast } from 'antd-mobile'
import qs from 'qs'
import Star from '../../components/consumer/star'


import { sendEvaluate, getEvaluate } from '../../api/consumer'


import './consumer.less'
interface ISprops {
    [propsName: string]: any
}
interface IState {
    starData: any,
    gd_qt: any,
    course_id: any,
    member_id: any
    evaluated: boolean,
    courseDate: any
}

export default class Evaluate extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            starData: {
                gd_jszyx: 5,
                gd_kcnr: 5,
                gd_xcfw: 5,
                gd_qybz: 5,
                gd_ckhj: 5
            },
            gd_qt: '',
            course_id: '',
            member_id: '',
            evaluated: false,
            courseDate: {}
        }
    }
    getEvaluateList() {
        const { course_id, member_id } = this.state;
        getEvaluate({
            course_id,
            member_id
        }).then((res) => {
            if (!res.status) {
                if (res.data.gd_jszyx) {//已评价 存在评价数据
                    let params = {
                        gd_jszyx: res.data.gd_jszyx,
                        gd_kcnr: res.data.gd_kcnr,
                        gd_xcfw: res.data.gd_xcfw,
                        gd_qybz: res.data.gd_qybz,
                        gd_ckhj: res.data.gd_ckhj,
                    }
                    this.setState({
                        starData: params,
                        gd_qt: res.data.gd_qt,
                        evaluated: true,
                        courseDate: res.data
                    }, () => {
                        console.log(this.state)
                    })
                } else {
                    this.setState({
                        evaluated: false,
                        courseDate: res.data
                    })
                }
            } else {
                Toast.fail(res["message"],2)
                this.props.history.go(-1)
            }
        })
    }
    componentWillMount() {
        document.title = '评价';
        this.getData()
    }
    getData = () => {
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        this.setState({
            course_id: data.course_id,
            member_id: data.member_id || localStorage.getItem('memberId')
        }, () => {
            this.getEvaluateList()
        })

    }
    changeStar = (data) => {
        let { starData } = this.state;
        starData = { ...starData, ...data }
        this.setState({
            starData
        }, () => {
            console.log(this.state.starData)
        })

    }
    changeText = (e) => {
        //将当前的合成事件从事件池中移除
        e.persist()
        this.setState({
            gd_qt: e.target.value
        })
    }
    submitEvaluate = () => {
        const { starData, gd_qt, course_id, member_id } = this.state;
        let params = {
            ...starData,
            gd_qt,
            course_id,
            member_id
        }
        sendEvaluate(params).then((data) => {
            if (!data.status) {
                Toast.info('提交成功', 3)
                this.props.history.go(-1)
            } else {
                Toast.fail(data["message"],2)
            }
        })

    }
    render() {
        const {
            gd_jszyx,
            gd_kcnr,
            gd_xcfw,
            gd_qybz,
            gd_ckhj
        } = this.state.starData
        const { gd_qt, evaluated, courseDate } = this.state;
        console.log(courseDate)
        return (
            <div className="evaluate">
                <div className="evaluate-detail" style={{ paddingBottom: '1rem' }}>
                    <div className="evaluate-header">
                        <p className="title">请您评价本节课<br/>鼓励我们做的更好</p>
                    </div>
                    <div className="evaluate-tips">
                        <p className="title">{courseDate.course_name || ''}</p>
                        <p className="teacher">主讲老师：{courseDate.teacher_name || ''}</p>
                    </div>
                    <div className="star clearfix">
                        <div className="left name">
                            *姓名
                        </div>
                        <div className="left">
                            <input type="text" name="" placeholder="请输入您的姓名" id=""/>
                        </div>
                    </div>
                    <Star title="讲师专业性" course="gd_jszyx" change={evaluated ? () => {} : this.changeStar} starNum={gd_jszyx} />
                    <Star title="课程内容" course="gd_kcnr" change={evaluated ? () => {} : this.changeStar} starNum={gd_kcnr} />
                    <Star title="现场服务" course="gd_xcfw" change={evaluated ? () => {} : this.changeStar} starNum={gd_xcfw} />
                    <Star title="对企业帮助" course="gd_qybz" change={evaluated ? () => {} : this.changeStar} starNum={gd_qybz} />
                    <Star title="参课环境" course="gd_ckhj" change={evaluated ? () => {} : this.changeStar} starNum={gd_ckhj} />
                    <div className="evaluate-a">
                        <textarea className="evaluate-area" disabled={evaluated} value={gd_qt} placeholder="您有任何意见或者建议，可以告诉我们..." onChange={(e) => this.changeText(e)}>
                        </textarea>
                    </div>
                </div>
                {
                    evaluated ?
                        <div></div>
                        :
                        <div className="evaluate-btn">
                            <Button className="btn" type="primary" onClick={this.submitEvaluate}>提交</Button>
                        </div>
                }

            </div>
        )
    }

}


