import * as React from "react";


import Star2 from '../../assets/img/consumer/Star2.png'
import Star1 from '../../assets/img/consumer/Star1.png'

interface ISprops {
    [propsName: string]: any
}
interface IState {
    checkedNumber: any
}

export default class Star extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            checkedNumber: 5
        }
    }
    componentDidMount() {
    }
    clickStar = (i,course) => {
        this.setState({
            checkedNumber: i
        })
        let obj = {}
        obj[course] = i;
        this.props.change(obj)
    }
    render() {
        const { title, course, starNum } = this.props;
        let { checkedNumber } = this.state;
        checkedNumber = starNum || checkedNumber;
        return (
            <div className="star clearfix">
                <div className="left name">
                    {title}
                </div>
                <div className="left">
                    <img src={checkedNumber >= 1 || starNum >= 1 ? Star1 : Star2} onClick={() => this.clickStar(1,course)} alt=""/>
                    <img src={checkedNumber >= 2 || starNum >= 2 ? Star1 : Star2} onClick={() => this.clickStar(2,course)} alt=""/>
                    <img src={checkedNumber >= 3 || starNum >= 3 ? Star1 : Star2} onClick={() => this.clickStar(3,course)} alt=""/>
                    <img src={checkedNumber >= 4 || starNum >= 4 ? Star1 : Star2} onClick={() => this.clickStar(4,course)} alt=""/>
                    <img src={checkedNumber >= 5 || starNum >= 5 ? Star1 : Star2} onClick={() => this.clickStar(5,course)} alt=""/>
                </div>
            </div>
        )
    }

}
